  box-sizing: border-box;
}

html {
  height: 100%;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  height: 100%;
  width: 100%;
  line-height: 1;
  font-family: "Open Sans", "Arial", sans-serif;
  color: #1a140d;
  font-size: 16px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  overflow-y: auto;
}

.main {
  width: 100%;
  min-height: 100vh;
  position: relative;
}

.form-container {
  max-width: 50rem;
  max-height: 40rem;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%);
  font-weight: 500;
  display: flex;
  justify-content: center;
  align-items: center;
}

.form-section {
  position: relative;
  max-width: 36rem;
  max-height: 33rem;
  width: 100%;
  height: 100%;
  background-color: #FFFFFF;
  padding: 40px;
  color: #111111;
  border-radius: 0.5rem;
}

.form-wrapper {
  width: 100%;
  height: 100%;
  max-height: 25rem;
  padding-top: 1.25rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.form {
  height: 90%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.form-title {
  text-align: center;
  font-weight: 400;
  line-height: 27px;
  letter-spacing: -0.3px;
  font-family: "PT Serif", serif;
}

.form-group {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 2.625rem;
}
.form-group label {
  color: #7C7C7C;
  text-transform: uppercase;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  font-weight: 300;
  font-size: 14px;
  min-height: 15px;
}
.form-group .input,
.form-group .textarea {
  font-size: 0.875rem;
  line-height: 1rem;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 400;
  border: none;
  border-bottom: 2px solid;
  border-image: linear-gradient(to right, #093f1c, #07e130);
  border-image-slice: 1;
  outline: none;
  background: inherit;
  text-overflow: ellipsis;
  max-width: 100%;
  margin-bottom: 7px;
}
.form-group .input.error-field,
.form-group .textarea.error-field {
  border-bottom: 2px solid #FF2828;
  border-image: none;
}
.form-group .textarea {
  min-height: 70px;
}
.form-group .input::placeholder,
.form-group .textarea::placeholder {
  color: #111111;
}

input[type=submit],
.btn-close {
  font-family: "PT Serif", serif;
  max-width: 170px;
  width: 100%;
  margin: 0 auto;
  min-height: 40px;
  background-image: linear-gradient(90deg, #08712d, #02b123);
  color: #FFFFFF;
  border: none;
  border-radius: 2px;
  cursor: pointer;
}
input[type=submit]:hover,
.btn-close:hover {
  background: #02b123;
}
input[type=submit]:active,
.btn-close:active {
  background: #08712d;
}

.btn-close {
  display: block;
}

.details {
  max-width: 150px;
  border: none;
  cursor: pointer;
  min-height: 25px;
  color: #5b2828;
  background: none;
}
.details:hover {
  border-radius: 2px;
  background-color: #c1f7c0;
  transition: 0.2s;
}
.details:active {
  background-color: #76b175;
}

.error {
  font-size: 14px;
  color: #FF2222;
}

.item4 .error {
  margin-top: -1rem;
}

.hidden {
  display: none;
}

.item4 {
  flex-grow: 0.4;
  gap: 1rem;
}

.popup {
  position: relative;
  z-index: 1;
  opacity: 0;
  visibility: hidden;
}

.opening {
  animation: fadeIn 0.2s ease-in forwards;
}

.closing {
  animation: fadeOut 0.2s ease-in forwards;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    visibility: hidden;
  }
  to {
    opacity: 1;
    visibility: visible;
  }
}
@keyframes fadeOut {
  from {
    opacity: 1;
    visibility: visible;
  }
  to {
    opacity: 0;
    visibility: hidden;
  }
}
.overlay {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(118, 177, 117, 0.8588235294);
  z-index: 1;
}
.overlay .popup-container {
  position: relative;
  margin: 0 auto;
  top: 25%;
  min-height: 300px;
  max-width: 465px;
  background-color: #FFFFFF;
  padding-top: 15px;
}
.overlay .popup-container .popup-title {
  text-align: center;
  font-size: 22px;
  padding-bottom: 1rem;
}
.overlay .popup-container ul li {
  padding-bottom: 1rem;
}

.notice-wrapper {
  position: absolute;
  top: 30px;
  left: 145px;
}

.notice {
  width: 300px;
  height: 50px;
  text-align: center;
  background-color: #07e130;
  border-radius: 5px;
  padding-top: 15px;
  position: relative;
  z-index: 5;
  color: #FFFFFF;
